import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'home',
    props: true,
    component: () => import('@/views/Home.vue') 
  },
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/Home.vue') 
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    props: true,
    component: () => import('@/views/Dashboard.vue') 
  },
  {
    path: '/signup',
    name: 'Signup',
    component: () => import('@/components/login/Signup.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/components/login/Login.vue')
  },
  {
    path: '/forgot-password',
    name: 'forgot-password',
    component: () => import('@/components/login/ForgotPassword.vue')
  },
  {
    path: '/vendedores',
    name: 'Vendedores',
    component: () => import( '@/views/Vendedores.vue')
  },
  {
    path: '/vendedores/ficha',
    name: 'FichaVendedor',
    component: () => import( '@/views/FichaVendedor.vue')
  },
  {
    path: '/user',
    name: 'Add',
    component: () => import( '@/components/usuario/UserCreate.vue'),
  },
  {
    path: '/list',
    name: 'List',
    component: () => import( '@/components/usuario/UserList.vue'),
  },
  {
    path: '/edit/:id',
    name: 'edit',
    component: () => import( '@/components/usuario/UserEdit.vue')
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
