<template>
  <div id="app">
    <div v-if="user">
       <NavBarVendedor />
    </div>
    <div v-else>
      <NavBar />
    </div>
   
    <router-view />
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import NavBarVendedor from '@/components/NavBarVendedor.vue'
import firebase from 'firebase'

export default {
  name: 'App',
  data() {
    return {
      user: null,
    }
  },
  components: {
    NavBar,
    NavBarVendedor
  },
  created() {
      firebase.auth().onAuthStateChanged((user) => {
         if (user) {
           this.user = user;
         }else {
           this.user = null;
         }
      });
  } 
}
</script>

